@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,500,700&display=swap);
body {
  margin: 0;
  font-size: 14px;
  font-family: "Montserrat", sans-serif;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-y: hidden;
}
* {
  font-family: "Montserrat", sans-serif;
}
button:focus {
  outline: none;
}

.navLayout {
