  text-decoration: underline; }

.formSection {
  width: 40%;
  margin: 10% 0 0 10%; }

.subDesc {
  line-height: 24px; }

#password {
  -webkit-text-security: disc; }

.form {
  margin: 10% 0 0 5%; }

.graphicBg {
  width: 100%;
  height: 20vh;
  margin: auto;
  bottom: 0;
  position: absolute;
  z-index: -1; }
  .graphicBg .blueBg {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 900px; }
  .graphicBg .formBlue {
    position: absolute;
    bottom: 100%;
    right: 20%;
    width: 180px; }
  .graphicBg .formOrange {
    position: absolute;
    bottom: 220%;
    right: 10%;
    width: 180px; }
  .graphicBg .coinBlue {
    position: absolute;
    bottom: 40%;
    right: 27%;
    width: 40px;
    -webkit-animation: coinBlue 3.6s ease-in-out infinite;
            animation: coinBlue 3.6s ease-in-out infinite; }
  .graphicBg .coinOrange {
    position: absolute;
    bottom: 220%;
    right: 30%;
    width: 40px;
    -webkit-animation: formBlue 3.6s ease-in-out infinite;
            animation: formBlue 3.6s ease-in-out infinite; }
  .graphicBg .coinOrangeSecond {
    position: absolute;
    bottom: 340%;
    right: 10%;
    width: 40px;
    -webkit-animation: formBlue 3.6s ease-in-out infinite;
            animation: formBlue 3.6s ease-in-out infinite; }
  .graphicBg .plant {
    position: absolute;
    bottom: 15%;
    right: 10%;
    width: 150px; }

@-webkit-keyframes formBlue {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  50% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px); }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

@keyframes formBlue {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  50% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px); }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

@-webkit-keyframes formOrange {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  50% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px); }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

@keyframes formOrange {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  50% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px); }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

@-webkit-keyframes coinBlue {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  50% {
    -webkit-transform: translateY(5px);
            transform: translateY(5px); }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

@keyframes coinBlue {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  50% {
    -webkit-transform: translateY(5px);
            transform: translateY(5px); }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

.errorMessage {
