.form {
  width: 300px; }
  .form .inputClass {
    position: relative;
    width: 100%;
    margin-bottom: 2rem; }
    .form .inputClass.-float > .field {
      height: 2.4rem;
      text-indent: 12px;
      border-radius: 100px;
      border: 1px solid #ddd; }
      .form .inputClass.-float > .field:focus, .form .inputClass.-float > .field:valid {
        border-color: #9aaec4; }
        .form .inputClass.-float > .field:focus + .title, .form .inputClass.-float > .field:valid + .title {
          top: 0;
          color: #9aaec4; }
      .form .inputClass.-float > .field + .title {
        left: 20px;
        padding: 0 0.5rem;
        background-color: #fff;
        border-radius: 2px; }
    .form .inputClass > .field {
      width: 100%;
      border: 0;
      outline: none; }
      .form .inputClass > .field + .title {
        position: absolute;
        top: 50%;
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
        color: #9e9e9e;
        -webkit-transition: 0.3s;
        transition: 0.3s;
        font-size: 0.9rem;
        pointer-events: none; }
      .form .inputClass > .field:focus + .title, .form .inputClass > .field:valid + .title {
        -webkit-transition: 0.3s;
        transition: 0.3s;
        font-size: 0.75rem; }

button {
  position: relative;
  width: 300px;
  height: 40px;
  border: none;
  color: #fff;
  background-image: -webkit-gradient(linear, left top, right top, from(#f89763), to(#ffc279));
  background-image: linear-gradient(to right, #f89763, #ffc279);
  border-radius: 100px;
  font-size: 16px;
  z-index: 1; }
  button:hover {
    cursor: pointer; }
  button::before {
    position: absolute;
    content: "";
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    border-radius: 100px;
    background-image: -webkit-gradient(linear, left top, right top, from(#db6e33), to(#f89763));
    background-image: linear-gradient(to right, #db6e33, #f89763);
    z-index: -1;
    -webkit-transition: opacity 0.3s ease-in-out;
    transition: opacity 0.3s ease-in-out;
    opacity: 0; }
  button:hover::before {
    opacity: 1; }

